class Scene {
  canvas: HTMLCanvasElement;
  options: {
    gravity?: Number
  };

  constructor (canvas: HTMLCanvasElement, options: Object = { gravity: 1 }) {
    this.canvas = canvas;
    this.options = options;
  }
}

class Body {
  x: Number;
  constructor () {
    this.x = 10;
  }
}


interface Body {
  x: Number
};

class Camera {
  x: Number;
  y: Number;

  options: {
    follow?: Body,
    followBounds?: {
      x: Number,
      y: Number
    },
    active?: Boolean,
  };

  constructor(x: Number, y: Number, options: Object) {
    this.x = x;
    this.y = y;
    this.options = options;
  }
}

var test = new Camera(10, 50, {
  follow: {
    asd: "bola"
  }
})

export var Lumi = {
  Scene: Scene,
  Camera: Camera
};